import AppServices from 'AppServices';
import isElectron from 'is-electron';
import * as THREE from './lib/three/build/three.module.js';
import * as environment from "./environment.js";
import * as rendering from "./renderer.js";
import * as prelude from './prelude.js';
import {OrbitControls} from './lib/three/examples/jsm/controls/OrbitControls.js';
import ko from 'knockout';
import audioManager from './AudioManager.ts';
import notifyAnalytics from './AnalyticsManager.ts';

// Allows knockout to expose ref to HTMLElement via `ref: variableNameAsObservable`
ko.bindingHandlers.ref = {
    init: function (element, valueAccessor, allBindings, viewModel, bindingContext) {
        var elemObservable = valueAccessor();
        elemObservable(element);
    }
};


var isOrbitEnabled = false;

export function AppViewModel(data, isPortraitModeRequired)
{
    var self = this;
    this.sections = data;
    this.loadingProgress = ko.observable("0%");
    this.lookAtPoint = new THREE.Vector3();

    this.showCloseButton = isElectron();
    if (isElectron()) {
      this.closeApp = AppServices.quit;
    }
    this.showDragBar = ko.pureComputed(() => this.showCloseButton && !this.isFullScreen(), this)

    // Calc current section
    this.currentSectionIndex = ko.observable(0);
    this.currentSection = ko.pureComputed(() => this.sections[this.currentSectionIndex()], this);
    // Calculate prev section
    let oldIndex = 0;
    this.currentSectionIndex.subscribe((i) => {
      this.prevSectionIndex(oldIndex);
      oldIndex = i;
    })
    this.prevSectionIndex = ko.observable(0);
    this.previousSection = ko.pureComputed(() => this.sections[this.prevSectionIndex()], this);
    // Calculate classes to animate section transitions
    this.prevVisibility = ko.observable('hidden');
    this.currentVisibility = ko.observable('visible');

    // These values get translated into css classes that animate the sections in and out
    this.animationDelay = ko.pureComputed(() => this.currentSection().key.includes('section') ? 2300 : 1000);
    let timeout1, timeout2;
    this.currentSectionIndex.subscribe(() => {
      // Clear timeouts to avoid overlaps
      clearTimeout(timeout1);
      clearTimeout(timeout2);

      this.prevVisibility('visible');
      this.currentVisibility('hidden');
      setTimeout(() => this.prevVisibility('hiding'));

      timeout1 = setTimeout(() => { 
        this.prevVisibility('hidden')
        this.currentVisibility('showing');
        timeout2 = setTimeout(() => {
          this.currentVisibility('visible');
        },400)
      }, this.animationDelay());
    })

    this.sections.forEach(section => section.parent = self);
    this.currentSection.subscribe(onCurrentSectionChanged);
    this.isPointCameraEnabled = true;
    this.isFinalReveal = false;
    this.isFullScreen = ko.observable(false);
    this.isCalculating = ko.observable(false);
    this.isSaveAvailable = ko.observable(false);
    this.isReseting = ko.observable(false);
    this.calculationProgress = ko.observable(0);
    this.controls = null;
    this.targetViewOffset = new THREE.Vector2();
    window.targetViewOffset = this.targetViewOffset;


    this.isPortraitMode = isPortraitModeRequired;
    this.showFullscreenButton = !isiOS();
    
    this.progressIndex = ko.pureComputed(() => {
        var section = this.currentSection();
        if(section.questions && section.questions.length > 0)
            return section.currentQuestion().progressIndex;
        else
            return -1;
    });

    // Grey overlay transparent of background
    this.isOverlayVisible = ko.pureComputed(() => {
        return false;
    });

    this.showNavProgression = ko.pureComputed(() => 
    {
        var section = this.currentSection();
        return section.showNavProgression;
    });
    this.hideBackButton = ko.pureComputed(() => this.currentSection().hideBackButton);
    this.hideNextButton = ko.pureComputed(() => this.isCalculating() || this.currentSection().hideNextButton);
    this.useInsightNextButton = ko.pureComputed(() => this.currentSection().key.includes('insights'));

    onCurrentSectionChanged(this.currentSection());

    // var query = window.matchMedia("(orientation: portrait)");
    // onMediaQueryChange(query);
    // query.addListener(onMediaQueryChange);
    updateViewOffset();

    function isiOS() 
    {
        return [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod'
        ].includes(navigator.platform)
        // iPad on iOS 13 detection
        || (navigator.userAgent.includes("Mac") && "ontouchend" in document)
    }

    function onCurrentSectionChanged(newSection)
    {
      notifyAnalytics('Navigation', 'Navigate to', newSection.key, { stage: newSection.stage() });
        if(self.currentSectionIndex() == self.sections.length-1)
            self.finalReveal();

        updateLookAtPoint();
        updateViewOffset();
    }

    // function onMediaQueryChange(e)
    // {
    //     self.isPortraitMode = e.matches
    //     updateViewOffset();
    // }

    function updateLookAtPoint()
    {
        var section = self.currentSection();
        if(section.modelOrigin)
            section.modelOrigin.getWorldPosition(self.lookAtPoint);
        else if(section.useFinalLookAt) {
            self.lookAtPoint.copy(environment.finalRevealOrigin.position);
      } else
            self.lookAtPoint.set(0, 1, -10);
    }

    this.triggerUpdateViewOffset = function()
    {
        updateViewOffset();
    }

    function updateViewOffset()
    {
        var section = self.currentSection();
        if(self.isPortraitMode)
            self.targetViewOffset.set(section.cameraOffset.portrait.x, section.cameraOffset.portrait.y);
        else
            self.targetViewOffset.set(section.cameraOffset.landscape.x, section.cameraOffset.landscape.y);
    }

    function toggleOrbitControls(isEnabled)
    {
        isOrbitEnabled = isEnabled;
        if(isEnabled)
        {
            // CONTROLS
            self.controls = new OrbitControls(rendering.camera, rendering.renderer.domElement);
            self.controls.target.copy(self.lookAtPoint);
            self.controls.enablePan = false;
            self.controls.enableZoom = true;
            self.controls.minDistance = 4;
            self.controls.maxDistance = self.isPortraitMode ? 7.2 : 9.5;
            self.controls.minPolarAngle = 0.8;
            self.controls.maxPolarAngle = 1.65;

            self.controls.update();
            
            self.isPointCameraEnabled = false;
        }
        else
        {
            self.controls.dispose();
            self.controls = null;
            self.isPointCameraEnabled = true;
            rendering.camera.position.set(0, 3, 0);
        }
    }

    this.toggleFullscreen = function()
    {
        self.isFullScreen(!self.isFullScreen());
        if(self.isFullScreen())
        {
            var el = document.getElementById("wrapper");
            if (el.requestFullscreen) 
                el.requestFullscreen();
            else if (el.webkitRequestFullscreen) /* Safari */
                el.webkitRequestFullscreen();
            else if (el.msRequestFullscreen) /* IE11 */
                el.msRequestFullscreen();
        }
        else
        {
            if (document.exitFullscreen)
                document.exitFullscreen();
            else if (document.webkitExitFullscreen) /* Safari */
                document.webkitExitFullscreen();
            else if (document.msExitFullscreen) /* IE11 */
                document.msExitFullscreen();
        }
    }

    this.reset = function()
    {
        audioManager.playFx('restart');
        notifyAnalytics('UserAction', 'Button Press', 'Restart')
        self.isReseting(true);
        self.currentSectionIndex(0);
        self.sections.forEach(section => section.reset());
        self.isFinalReveal = false;
        environment.finalRevealOrigin.rotation.set(0, 0, 0);
        toggleOrbitControls(false);
        self.isReseting(false);
    }

    this.getSectionByKey = function(sectionKey)
    {
        for(var i=0;i<self.sections.length;i++)
        {
            if(self.sections[i].key == sectionKey) return { section: self.sections[i], index: i };
        }
        return null;
    }

    this.goToQuestion = function(questionIndex, a, b, c)
    {
        var sectionNumber = Math.ceil(questionIndex / 2);
        var questionNumber = (questionIndex-1) % 2;
        var sectionKey = "section" + sectionNumber;
        var section;
        var sectionIndex;
        for(var i=0;i<self.sections.length;i++)
        {
            if(self.sections[i].key == sectionKey)
            {
                section = self.sections[i];
                sectionIndex = i;
                break;
            }
        }
        section.showQuestion(questionNumber);
        self.currentSectionIndex(sectionIndex);

    }

    this.next = function()
    {
        var index = self.currentSectionIndex();
        if(index < self.sections.length-1) self.currentSectionIndex(index + 1);
        // const currentSection = self.currentSection();
        // if(currentSection && currentSection.key === 'results') self.startCalculating();
    }

    this.prev = function()
    {
        var index = self.currentSectionIndex();
        if(index > 0) self.currentSectionIndex(index - 1);
    }

    this.back = function()
    {
        self.currentSection().prev();
    }

    this.continue = function()
    {
        self.currentSection().next();
    }

    this.findOutMore = function() {
      audioManager.playFx('findout');
      notifyAnalytics('UserAction', 'Button Press', 'View Insights');
      self.continue();
    }

    this.continueToResult = function() {
      audioManager.playFx('next');
      notifyAnalytics('UserAction', 'Button Press', 'Skip Insights');
      const resultsIndex = self.sections.findIndex(s => s.key === 'results');
      self.currentSectionIndex(resultsIndex);
    }

    this.startCalculating = function()
    {
        self.isCalculating(true);
        self.calculationProgress(0);

        var progress = 0;
        var totalDuration = 6000; //Milliseconds
        var tickDuration = 10;
        var increment = 100 / (totalDuration / tickDuration);
        var timer = setInterval(() =>
        {
            progress += increment;
            if(progress < 100)
            {
                self.calculationProgress(Math.round(progress));
                prelude.setRevealFactor(progress.remap(0, 100, 0, 1));
            }
            else
            {
                clearInterval(timer);
                self.isCalculating(false);
            }
        }, tickDuration);

    }

    this.finalReveal = function()
    {
         var revealSection = self.getSectionByKey("results");
         self.currentSectionIndex(revealSection.index);
        toggleOrbitControls(true);
        self.isFinalReveal = true;
        self.isSaveAvailable(false);
    }

    this.saveImage = function()
    {
        if(!self.isSaveAvailable()) return;
        audioManager.playFx('save');
        notifyAnalytics('UserAction', 'Button Press', 'Save Image');

        self.isSaveAvailable(false);

        const canvas = document.createElement('canvas');
        canvas.id = 'saveCanvas';
        canvas.setAttribute('width', '1024');
        canvas.setAttribute('height', '1024');
        canvas.style.display = 'none';
        canvas.style.position = 'absolute';
        canvas.style.top = '0';
        canvas.style.left = '0';
        document.body.appendChild(canvas);
        var context = canvas.getContext("2d");
        var originalSize = new THREE.Vector2();
        var originalZoom = rendering.camera.zoom;
        var originalPosition = new THREE.Vector3();
        var originalRotation = new THREE.Euler();
        var strMime = "image/jpeg";
        var img = document.getElementById("saveImg");
        var logo = document.getElementById("saveLogo");

        originalPosition.copy(rendering.camera.position);
        originalRotation.copy(rendering.camera.rotation);

        rendering.camera.position.set(0, 3, 0);
        rendering.camera.rotation.set(-3, 0, 3.14);
        rendering.camera.updateMatrix();

        rendering.renderer.getSize(originalSize);
        rendering.camera.aspect = 1;
        rendering.camera.setViewOffset(1, 1, 0, 0.07, 1, 1);
        rendering.camera.zoom = 1;
        rendering.camera.updateProjectionMatrix();
        rendering.renderer.setSize(canvas.width, canvas.height);
        rendering.renderer.render(rendering.scene, rendering.camera);
        rendering.renderer.render(rendering.scene, rendering.camera);
        rendering.renderer.render(rendering.scene, rendering.camera);
        
        var imgData = rendering.renderer.domElement.toDataURL(strMime);
        
        img.onload =() =>
        {
            var width = canvas.width * 1;
            var height = canvas.height * 1;
            context.drawImage(img, 0, 0, width, height);
            context.drawImage(logo, 0, 0, width, height);

            var canvasData = canvas.toDataURL(strMime);
            var link = document.getElementById('downloadLink');
            link.setAttribute('download', 'REFLCT.jpg');
            link.setAttribute('href', canvasData.replace("image/jpeg", "image/octet-stream"));
            link.click();

            self.isSaveAvailable(true);
        }       
        img.src = imgData;

        rendering.camera.position.copy(originalPosition);
        rendering.camera.rotation.copy(originalRotation);
        rendering.camera.aspect = originalSize.x / originalSize.y;
        rendering.camera.zoom = originalZoom;
        rendering.camera.updateProjectionMatrix();
        rendering.renderer.setSize(originalSize.x, originalSize.y);

        document.body.removeChild(canvas);
    }
}

