export function SectionViewModel(data)
{
    var self = this;
    this.key = null;
    this.title = "";
    this.info = "";
    this.questions = null;
    this.parent = null;
    this.stage = "info";
    this.modelOrigin = null;
    this.useFinalLookAt = false;
    this.lightColor = 0xffffff;
    this.showNavProgression = true;
    this.hideBackButton = false;
    this.hideNextButton = false;
    this.sliderClass = "";
    this.zoom = 1;
    this.cameraOffset = { landscape: { x: 0.25, y: 0.035 }, portrait: { x: 0, y: 0.3 }};

    Object.assign(this, data);
    
    this.isLoading = ko.observable(false);
    this.shouldShowQuestions = ko.observable(false);
    this.currentQuestionIndex = ko.observable(0);
    this.lightColor = new THREE.Color(this.lightColor);
    this.stage = ko.observable(this.stage);

    if(this.questions) this.questions.forEach(question => question.parent = self);
    if(this.insights) {
      this.insights.forEach(insight => insight.parent = self);
      self.stage('insights');
    }
    this.stage.subscribe(newValue => {
      if (self.parent && self.parent.isReseting()) return;
      const extras = {
        stage: newValue,
      }
      if (newValue === 'questions')
        extras.question = self.currentQuestionIndex();
      notifyAnalytics('Navigation', 'Navigate to', self.key, extras);
    }, this);
    this.currentQuestionIndex.subscribe(newQuestionIndex => {
      if (self.parent && self.parent.isReseting()) return;
      const extras = {
        stage: self.stage(),
        question: newQuestionIndex,
      }
      notifyAnalytics('Navigation', 'Navigate to', self.key, extras);
    })

    this.currentQuestion = ko.pureComputed(() => this.questions[this.currentQuestionIndex()], this);
    this.isLastQuestion = ko.pureComputed(() => this.currentQuestionIndex() >= this.questions.length - 1, this);

    this.currentInsightIndex = ko.observable(0);
    this.currentInsight = ko.pureComputed(() => this.insights[this.currentInsightIndex()], this);
    this.isLastInsight = ko.pureComputed(() => this.currentInsightIndex() >= this.insights.length - 1, this);
    this.insightQuestions = ko.pureComputed(() => {
      if (!this.key.includes('insights')) return undefined;
      const sectionNumber = this.key.charAt(this.key.length-1);
      const associatedSection = this.parent.sections.find(s => s.key === `section${sectionNumber}`);

      console.log(sectionNumber, associatedSection);
      if (!associatedSection || !associatedSection.questions) {
        console.error(`Error getting question responses for ${this.key}`);
        return undefined;
      }

      console.log(this.currentInsight().classificationsOverride);
      console.log(associatedSection.questions);

      // Use classificationsOverride if supplied in insight model
      console.log(`Using classificationsOverride? ${this.currentInsight() && this.currentInsight().classificationsOverride !== undefined} with index ${associatedSection.questions[0].valueIndex()}`);

      const value = this.currentInsight() && this.currentInsight().classificationsOverride !== undefined
        ? this.currentInsight().classificationsOverride[associatedSection.questions[0].valueIndex()]
        : associatedSection.questions[0].valueDescription();

      const feeling = this.currentInsight() && this.currentInsight().feelingsOverride !== undefined
        ? this.currentInsight().feelingsOverride[associatedSection.questions[1].valueIndex()]
        : associatedSection.questions[1].valueDescription();

      return {
        value,
        feeling,
      }
    });

    this.reset = function()
    {
        self.shouldShowQuestions(false);
        self.currentQuestionIndex(0);
        self.stage(data.stage ? data.stage : "info");
        if(self.questions) self.questions.forEach(question => question.reset());
    }

    this.showQuestion = function(questionNumber)
    {
        self.stage("questions");
        self.currentQuestionIndex(questionNumber);
    }

    this.showInsights = (insightNumber) => {
      self.stage('insights');
      self.currentInsightIndex(insightNumber);
    }


    this.next = () => {
      if (self.key !== 'calculating') audioManager.playFx('next');
        switch(self.stage())
        {
            case "info":
                if(self.questions)
                    self.stage("questions");
                else
                    self.parent.next();
                break;
            case "questions":
                if(self.questions) {
                  var index = self.currentQuestionIndex();
                  if(index < self.questions.length-1) 
                      self.currentQuestionIndex(index + 1);
                  else {
                    self.notifyQuestionsValues();
                    self.parent.next();
                  }
                }
                else
                  self.parent.next();
                break;
            case 'insights':
              if (this.insights) {
                const index = this.currentInsightIndex();
                if (self.isLastInsight()) {
                  self.parent.next();
                } else {
                  self.currentInsightIndex(index + 1)
                }
              }
              break;
        }
    }

    this.prev = function()
    {
      audioManager.playFx('prev');
      var index = self.currentQuestionIndex();
      console.log(index);
      console.log(self.stage());
      if(index > 0) {
          self.currentQuestionIndex(index - 1);
      } else if (self.stage() == 'info') {
        self.parent.prev();
      } else if(self.stage() == "questions") {
          self.stage("info");
      } else if (self.stage() === 'insights') {
        const index = self.currentInsightIndex();
        if (index === 0) {
          console.log('prev');
          self.parent.prev();
        } else {
          self.currentInsightIndex(index - 1);
        }
      }
    }

    this.notifyQuestionsValues = function() {
      const getHumanReadableValue = (q) => {
        let v = q.valueDescription();
        const unit = q.currentUnit();
        if (unit) v += ` ${unit}`;
        return v;
      }
      const q0 = self.questions[0];
      const q1 = self.questions[1];
      const questions = {
        question0: q0.value(),
        question0response: getHumanReadableValue(q0),
      }
      if (q1) {
        questions.question1 = q1.value();
        questions.question1response = getHumanReadableValue(q1);
      }
      notifyAnalytics('UserInput', 'Question result', self.key, questions);
    }
}

