export function QuestionViewModel(data)
{
    var self = this;
    this.text = "";
    this.classifications = null;
    this.value = 0;
    this.parent = null;
    this.progressIndex = 0;

    Object.assign(this, data);

    this.value = ko.observable(data.value);
    this.hasInput = ko.observable(false);
    this.value.subscribe(newValue => { 
      this.hasInput(true);
    });

    this.valueIndex = ko.pureComputed(() => {
      return Math.floor(self.value() * (self.classifications.length * 0.9999))
    })
    this.valueDescription = ko.pureComputed(() => {
      return self.classifications[this.valueIndex()];
    })
    this.currentUnit = ko.pureComputed(() => {
        if (typeof this.unit === 'string') {
          return this.unit;
        } else if (typeof this.unit === 'object') {
          if (this.unit.singular && this.unit.plural) {
            var index = this.valueIndex();
            return index === 0 ? this.unit.singular : this.unit.plural;
          }
        } 
        return undefined;
    });

    this.reset = function()
    {
        self.value(data.value ? data.value : 0);
        self.hasInput(false);
    }

    this.next = function()
    {
      self.parent.next();
    }

    this.prev = function()
    {
        self.parent.prev();
    }
}

export function InsightViewModel(data) {
  var self = this;
  // Defaults
  this.insight = null;

  this.descriptionContainerEl = ko.observable();
  let oldEl = undefined;
  let element;
  this.descriptionContainerEl.subscribe((el, oldEl) => {
    element = el;
    el.addEventListener('scroll', updateScrollDistance);
    if (oldEl)
      oldEl.removeEventListener('scroll', updateScrollDistance);
    oldEl = el;

    // Required to wait for dom to paint
    setTimeout(() => {
      updateScrollDistance();
    })
  })

  this.topDistance = ko.observable();
  this.bottomDistance = ko.observable();
  const updateScrollDistance = () => {
    const topDistance = element.scrollTop / element.scrollHeight * 100;
    this.topDistance(`${Math.min(topDistance, 10)}%`);
    const bottomDistance = (element.scrollTop + element.clientHeight) / element.scrollHeight * 100;
    this.bottomDistance(`${Math.max(bottomDistance, 90)}%`);
  }

  // Super hacky use of visible: binding to pass the element into the viewmodel
  this.setElement = (el) => {
    this.descriptionContainerEl(el);
    return true;
  }

  Object.assign(this, data);
}
