import * as THREE from './lib/three/build/three.module.js';
import Stats from './lib/three/examples/jsm/libs/stats.module.js';

const debugMode = false;

export var widget;
export var camera;
export var scene;
export var renderer;
export var stats;
export var composer;
export var isPortraitMode;
var container;
var worldContainer;
var orbZone;
var overviewCamera;

let width, height;
export function initialize(worldContainer)
{
    worldContainer = document.getElementById("worldContainer");
    orbZone = document.getElementById("orbZone");
    
    container = worldContainer;
    camera = new THREE.PerspectiveCamera(30, window.innerWidth / window.innerHeight, 1, 5000);
    camera.zoom = 1.75;
    camera.position.set(0, 3, 0);
    scene = new THREE.Scene();
    
    overviewCamera = new THREE.PerspectiveCamera(60, window.innerWidth/ window.innerHeight, 1, 5000);
    overviewCamera.position.set(0, 20, 0);
    overviewCamera.lookAt(new THREE.Vector3(0, 0, 0));
    if (debugMode) {
      const cameraHelper = new THREE.CameraHelper(camera);
      scene.add(cameraHelper);
    }


    // RENDERER
    width = container.clientWidth;
    height = container.clientHeight;
    renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.debug.checkShaderErrors = process.env.NODE_ENV !== 'production';
    renderer.autoClear = false;
    renderer.autoClearColor = false;
    renderer.autoClearDepth = false;
    renderer.autoClearStencil = false;
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(container.clientWidth, container.clientHeight);
    container.appendChild(renderer.domElement);
    renderer.shadowMap.enabled = true;
    renderer.outputEncoding = THREE.sRGBEncoding;
    renderer.toneMapping = THREE.ACESFilmicToneMapping;
    
    // STATS
    // stats = new Stats();
    // container.appendChild(stats.dom);

	window.addEventListener('resize', onWindowResize, false);
 //    var query = window.matchMedia("(orientation: portrait)");
 //    onMediaQueryChange(query);
 //    query.addListener(onMediaQueryChange);
}

export function onWindowResize() 
{
  width = container.clientWidth;
  height = container.clientHeight;
    camera.aspect = container.clientWidth / container.clientHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(container.clientWidth, container.clientHeight);
    //document.querySelector(".mainText").innerText = container.clientWidth + "x" + container.clientHeight;
    //updateViewOffset();
}

export function render() {
  renderer.clear();
  renderer.setViewport(0, 0, width, height);
  renderer.render(scene,camera);

  if (debugMode) {
    renderer.clearDepth();
    const x = width - (width * 0.4);
    const y = height - (height * 0.4);
    renderer.setViewport(x, y, width * 0.4, height * 0.4);
    renderer.render(scene, overviewCamera);
  }
}

