import * as THREE from './lib/three/build/three.module.js';
import { GLTFLoader } from './lib/three/examples/jsm/loaders/GLTFLoader.js';
import * as environment from "./environment.js";
import { Animator } from "./revealAnimator.js";
import audioManager from './AudioManager.ts';
import {isPortraitMode} from './renderer.js';

const loader = new GLTFLoader();
var mesh = null;
var shadow = null;
var scene = null;
var viewModel = null;
var fillWidth = 0.9;
var currentFillAmount = 0;
var currentMorph = 0;
var center;
var sectionPlacement;
var origin;
var revealAnimator;
var isRevealed = false;
var worldForward = new THREE.Vector3(0, 0, 1);
var originForward = new THREE.Vector3();
var axis = new THREE.Vector3(-1, 1, 0);
axis.normalize();

export function	initialize(providedScene, mainViewModel, inRevealAnimator)
{
    return new Promise((resolve, reject) => 
    {
        viewModel = mainViewModel.getSectionByKey("section4").section;
        currentFillAmount = viewModel.questions[0].value();
        currentMorph = viewModel.questions[1].value();
        mainViewModel.currentSection.subscribe(onCurrentSectionChanged);
        viewModel.isLoading(true);

        scene = providedScene;
        center = new THREE.Object3D();
        origin = new THREE.Object3D();
        center.name = 'Stage4 Origin';
        sectionPlacement = new THREE.Object3D();
        sectionPlacement.position.set(0, 2, -10);
        center.rotation.set(0, 0.628 * -4, 0);
        sectionPlacement.add(origin);
        center.add(sectionPlacement);
        scene.add(center);
        center.name = 'Stage4 Center';
        viewModel.modelOrigin = origin;
        revealAnimator = inRevealAnimator;
        revealAnimator.setSourceSection(viewModel);

        Promise.all([
            loadModel("static/models/q7.glb"),
            getMainMaterial(),
            loadModel("static/models/q7.shadow.glb"),
            getShadowMaterial(),
            audioManager.addStageSound(4, 'static/audio/s4_wellbeing.mp3'),
        ]).then(results =>
        {
            var model = results[0];
            var material = results[1];
            var shadowModel = results[2];
            var shadowMat = results[3];
            
            mesh = model.scene.children[0];
            mesh.material = material.main;
            origin.add(mesh);

            //shadow
            shadow = shadowModel.scene.children[0];
            shadow.position.set(0, -1.9, 0);
            shadow.material = shadowMat;
            scene.add(shadow);

            viewModel.isLoading(false);
            if(mainViewModel.currentSection() == mainViewModel) show();
            resolve();
        });
    });
}

let hasMovedToOrigin = false;
function onCurrentSectionChanged(newSection)
{
    if (newSection.key.includes('insights')) {
      moveToFinal();
    } else if(newSection.key == "results") {
        moveToFinal();
        revealAtFinal();
    } else {
      reset();
    }
}

export function reset()
{
    hasMovedToOrigin = false;
    environment.finalRevealOrigin.remove(origin);
    sectionPlacement.add(origin);
    origin.scale.set(1, 1, 1);
    origin.rotation.set(0, 0, 0);
    isRevealed = false;
}

export function moveToFinal() {
  if (!hasMovedToOrigin) {
    hasMovedToOrigin = true;
    environment.finalRevealOrigin.add(origin);
  }
}

export function revealAtFinal()
{
    isRevealed = true;
}

export function update(clock, delta)
{
    revealAnimator.update(clock);

    if(mesh && mesh.material && mesh.material.userData && mesh.material.userData.fillAmount)
    {
        if(mesh.material.envMap != environment.envMap)
        {
            mesh.material.envMap = environment.envMap;
            mesh.material.needsUpdate = true;
        }
        
        currentFillAmount = THREE.Math.lerp(currentFillAmount, viewModel.questions[0].value(), 1.5 * delta);
        currentMorph = THREE.Math.lerp(currentMorph, viewModel.questions[1].value(), 1.5 * delta);
        audioManager.updateStageSound(4, { 
          volume: viewModel.questions[0].value() * 0.5 + 0.5,
          frequency: viewModel.questions[1].value() * 2200 + 800,
        });

        mesh.material.userData.time.value = clock.elapsedTime;
        mesh.material.userData.fillAmount.value = currentFillAmount;
        mesh.material.userData.fillWidth.value = fillWidth;
        mesh.material.userData.morph.value = currentMorph;

        if(!isRevealed) {
            origin.rotation.set(0, currentMorph * 0.5 + currentFillAmount * 0.3, 0, 'XYZ');
            if (hasMovedToOrigin) {
              const slideValue = (1 - revealAnimator.mix) * 0.2;
              if (isPortraitMode) {
                origin.position.set(0, slideValue, 0);
              } else {
                origin.position.set(slideValue, 0, 0);
              }
            }
        } else
            origin.rotateOnAxis(axis, delta * -0.083, 'XYZ');
    }

    if(shadow && shadow.material && shadow.material.userData && shadow.material.userData.blend)
    {
        shadow.material.userData.blend.value = currentFillAmount;
        origin.getWorldPosition(shadow.position);
        shadow.position.y = 0.1;
        shadow.scale.copy(origin.scale);
        origin.getWorldDirection(originForward);
        originForward.y = 0;
        originForward.normalize();
        shadow.quaternion.setFromUnitVectors(worldForward, originForward);
    }
}

function getMainMaterial()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
        	fetch("static/stage4/q7.prefix.vertex").then(res => res.text()),
            fetch("static/stage4/q7.vertex").then(res => res.text()),
            fetch("static/stage4/q7.prefix.frag").then(res => res.text()),
            fetch("static/stage4/q7.frag").then(res => res.text())
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];
            var fragPrefix = results[2];
            var fragBody = results[3];

            var material = new THREE.MeshPhysicalMaterial(
            {
                color: 0xffffff,
                metalness: 0,
                roughness: 0.3,
                vertexColors: true
            });

            material.userData.fillAmount = { value: 0.9 };
            material.userData.fillWidth = { value: 0.0 };
            material.userData.morph = { value: 0.0 }
            material.userData.time = { value: 0 };

            material.customProgramCacheKey = () => "q7";
            material.onBeforeCompile = shader =>
            {
                shader.uniforms.fillAmount = material.userData.fillAmount;
                shader.uniforms.fillWidth = material.userData.fillWidth;
                shader.uniforms.morph = material.userData.morph;
                shader.uniforms.time = material.userData.time;

                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <morphtarget_vertex>", body);
                shader.fragmentShader = fragPrefix + "\n" + shader.fragmentShader.replace("#include <metalnessmap_fragment>", fragBody);
            };

            resolve({ main: material });
        });
    });
}

function loadModel(url)
{
    return new Promise((resolve, reject) => loader.load(url, (gltf) => resolve(gltf)));
}

function getShadowMaterial()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
            fetch("static/stage1/shadow.prefix.vertex").then(res => res.text()),
            fetch("static/stage1/shadow.vertex").then(res => res.text())
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];
            var material = new THREE.MeshBasicMaterial({ vertexColors: true, transparent: true, depthWrite: false, blending: THREE.SubtractiveBlending, color: 0x222222 });

            material.userData.blend = { value: 0 };

            material.customProgramCacheKey = () => "shadow";
            material.onBeforeCompile = shader =>
            {
                shader.uniforms.blend = material.userData.blend;
                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", "#include <project_vertex>\n" + body);
            };

            resolve(material);
        });
    });
}
