import * as THREE from './lib/three/build/three.module.js';

export function Animator(finalScale, finalRotation)
{
	var self = this;
	var startTime;
	var startSize = new THREE.Vector3(0, 0, 0);
	var elapsed = 0;
	var t = 0;
  self.mix = t;
	var origin;
	var rot = new THREE.Quaternion().setFromEuler(new THREE.Euler(0, 0, 0));
  var initialRot = rot.clone();
  var initialScale = new THREE.Vector3();
	var startRot = new THREE.Quaternion().setFromEuler(new THREE.Euler(0.5, 2, 0.5));
  
	var targetRot = new THREE.Quaternion().setFromEuler(new THREE.Euler(finalRotation.x, finalRotation.y, finalRotation.z));
	var sourceSection;
  let duration = 1;

	self.isActive = false;

	self.setSourceSection = function(inSourceSection)
	{
		sourceSection = inSourceSection;
		origin = inSourceSection.modelOrigin;
	}
	
	self.start = function(inCurrentTime, animationDuration, delay = 0)
	{
    console.log('start');
    initialScale.copy(origin.scale);
    initialRot.copy(rot);
		// origin.scale.copy(startSize);
		// origin.quaternion.setFromEuler(new THREE.Euler(0, 0, 0));
		rot.copy(initialRot);
    duration = animationDuration + delay ;

		startTime = inCurrentTime + delay;
		self.isActive = true;
		self.isInverse = false;
	}
  self.startInverse = function(inCurrentTime, animationDuration, delay = 0) {
    console.log('Reveal inverse');
    initialScale.copy(origin.scale);
    initialRot.copy(rot);
    rot.copy(initialRot);

    startTime = inCurrentTime + delay;
    duration = animationDuration;
    self.isActive = true;
    self.isInverse = true;
  }

  self.revealImmediate = function () {
    console.log('Reveal');
    origin.scale.copy(finalScale);
    origin.quaternion.copy(targetRot);
    rot.copy(origin.quaternion);
    origin.visible = true;
    self.isActive = false;
  }

  self.hideImmediate = function () {
    console.log('hideImmediate');
    origin.scale.copy(startSize);
    origin.quaternion.copy(startRot);
    rot.copy(origin.quaternion);
    origin.visible = false;
    self.isActive = false;
  }

	self.update = function(clock)
	{
		if(!self.isActive) return;

		if(clock.elapsedTime >= startTime)
		{
			elapsed = clock.elapsedTime - startTime;
	        t = elapsed / duration;
	        t = 1 - (1 - t) * (1 - t); //Easing
          origin.visible = true;
          self.mix = self.isInverse ? 1 - t : t;
	        
          if (clock.elapsedTime <= startTime + duration) {
            if (self.isInverse) {
              THREE.Quaternion.slerp(initialRot, startRot, rot, t)
              origin.scale.lerpVectors(initialScale, startSize, t);
            } else {
              THREE.Quaternion.slerp(initialRot, targetRot, rot, t)
              origin.scale.lerpVectors(initialScale, finalScale, t);
            }
            origin.quaternion.copy(rot);
          } else {
	            self.isActive = false;

              if (!self.isInverse) {
                origin.scale.copy(finalScale);
                origin.quaternion.copy(targetRot);
              } else {
                origin.visible = false;
                origin.scale.copy(startSize);
                origin.quaternion.copy(startRot);
              }
              self.isInverse = false;
          }
	    }
	}
}
