import * as THREE from './lib/three/build/three.module.js';
import { GLTFLoader } from './lib/three/examples/jsm/loaders/GLTFLoader.js';
import * as environment from "./environment.js";
import * as rendering from "./renderer.js";
import audioManager from './AudioManager.ts';

const loader = new GLTFLoader();
var haloMesh = null;
var orbMesh = null;
var splashMesh = null;
var geo = null;
var scene = null;
var viewModel = null;
var center;
var sectionPlacement;
var origin;
var sprite;
var revealFactor = 1;
var hideAnimator

export var isLoaded = false;

export function	initialize(providedScene, mainViewModel, inHideAnimator)
{
    return new Promise((resolve) => 
    {
        hideAnimator = inHideAnimator;
        hideAnimator.setCallback(setRevealFactor);
        viewModel = mainViewModel.getSectionByKey("prelude").section;
        mainViewModel.currentSection.subscribe(onCurrentSectionChanged);
        viewModel.isLoading(true);

        scene = providedScene;
        center = new THREE.Object3D();
        origin = new THREE.Object3D();
        origin.name = 'Prelude Origin';
        sectionPlacement = new THREE.Object3D();
        sectionPlacement.position.set(0, 1, -10);
        center.rotation.set(0, 0, 0);
        sectionPlacement.add(origin);
        center.add(sectionPlacement);
        center.name = 'PreludeCenter';
        scene.add(center);
        viewModel.modelOrigin = sectionPlacement;

        const soundPromise = audioManager.addStageSound(7, 'static/audio/s7_background.mp3');
        const loadPromise = loadPrelude();
        Promise.all([soundPromise, loadPromise]).then(() => 
        {
            viewModel.isLoading(false);
            if(mainViewModel.currentSection() == mainViewModel) show();
            show();

            isLoaded = true;
            resolve();
        });
    });
}

let hasMovedToOrigin = false;
function onCurrentSectionChanged(newSection)
{
  const {key} = newSection;
  if (key.includes('calculating') || key.includes('insights') || key === 'results')
    moveToFinal();
  else {
    reset();
  }
}

export function moveToFinal() {
  if (!hasMovedToOrigin) {
    hasMovedToOrigin = true;
    environment.finalRevealOrigin.add(origin);
  }
}

export function setRevealFactor(inRevealFactor) {
    revealFactor = inRevealFactor;
    if(revealFactor == 0) hide();
    else show();
}

export function reset()
{
    show();
    revealFactor = 1;
    if (hasMovedToOrigin) {
      sectionPlacement.add(origin);
      hasMovedToOrigin = false;
    }
}

export function show()
{
    origin.visible = true;
}

export function hide()
{
    origin.visible = false;
}

function loadPrelude()
{
    return new Promise((resolve) => 
    {
        Promise.all([
            loadModel("static/models/prelude.points.glb"),
            loadModel("static/models/prelude.sphere.glb"),
            getOrbMaterial(),
            getHaloMaterial(),
            getSplashMaterial()
        ]).then(results => {
            var pointsModel = results[0];
            var instanceModel = results[1];
            var orbMaterial = results[2].main;   
            var haloMaterial = results[3].main;
            var splashMaterial = results[4].main;
            var instanceGeo = instanceModel.scene.children[0].geometry;
            var positionData = pointsModel.scene.children[0].geometry.attributes.position;
            var instanceCount = positionData.count; 
            const position = new THREE.Vector3();
            const rotation = new THREE.Euler();
            const quaternion = new THREE.Quaternion();
            const scale = new THREE.Vector3(.2, .2, .2);
            const matrix = new THREE.Matrix4();
            const scalar = 1;

            //Orbs
            orbMesh = new THREE.InstancedMesh(instanceGeo, orbMaterial, instanceCount);
            orbMesh.scale.copy(scale);
            for(var i=0;i<instanceCount;i++)
            {
                position.set(positionData.array[i*3+0] * scalar, positionData.array[i*3+1] * scalar, positionData.array[i*3+2] * scalar);
                matrix.compose(position, quaternion, scale);
                orbMesh.setMatrixAt(i, matrix);
            }
            instanceGeo.setAttribute("instanceAxis", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._axis.array, 3));
            instanceGeo.setAttribute("instancePScale", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._pscale.array, 1));
            instanceGeo.setAttribute("instanceSpeed", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._speed.array, 1));
            orbMesh.needsUpdate = true;
            origin.add(orbMesh);

            //Halo
            haloMesh = new THREE.Points(pointsModel.scene.children[0].geometry, haloMaterial);
            haloMesh.scale.copy(scale);
            origin.add(haloMesh);

            //Ground Splash
            var planeGeo = new THREE.PlaneBufferGeometry(4, 4);
            splashMesh = new THREE.InstancedMesh(planeGeo, splashMaterial, instanceCount);
            splashMesh.scale.copy(scale);
            splashMesh.position.set(0, -sectionPlacement.position.y + 0.1, 0);
            quaternion.setFromEuler(new THREE.Euler(-1.57, 0, 0));
            scale.set(1, 1, 1);
            var instanceColor = new THREE.Color(0xffffff);
            for(var i=0;i<instanceCount;i++)
            {
                position.set(0, 0, 0);
                //position.set(positionData.array[i*3+0] * scalar, positionData.array[i*3+1] * scalar, positionData.array[i*3+2] * scalar);
                matrix.compose(position, quaternion, scale);
                splashMesh.setMatrixAt(i, matrix);
                splashMesh.setColorAt(i, instanceColor);
            }
            splashMesh.instanceColor.needsUpdate = true;

            planeGeo.setAttribute("instancePosition", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes.position.array, 3));
            planeGeo.setAttribute("instanceAxis", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._axis.array, 3));
            planeGeo.setAttribute("instancePScale", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._pscale.array, 1));
            planeGeo.setAttribute("instanceSpeed", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._speed.array, 1));
            splashMesh.needsUpdate = true;
            scene.add(splashMesh);

            resolve();
        });
    });
}

function loadModel(url)
{
    return new Promise((resolve, reject) => loader.load(url, (gltf) => resolve(gltf)));
}

export function update(clock, delta)
{
    var brightness = viewModel.questions[0].value();
    audioManager.updateStageSound(7, { volume: viewModel.questions[0].value() * 0.5 + 0.5 });
    var zoomCompensation;
    currentColor.copy(dimColor);
    currentColor.lerp(brightColor, brightness);

    hideAnimator.update(clock);

    if(orbMesh && orbMesh.material && orbMesh.material.userData && orbMesh.material.userData.time)
    {
        // if(orbMesh.material.envMap != environment.envMap)
        // {
        //     orbMesh.material.envMap = environment.envMap;
        //     orbMesh.material.needsUpdate = true;
        // }

        orbMesh.material.emissive.copy(orbEmissive);
        orbMesh.material.emissive.multiplyScalar(remap(brightness, 0, 1, 0.5, 2));

        orbMesh.material.userData.time.value = clock.elapsedTime;
        orbMesh.material.userData.revealFactor.value = revealFactor;
    }

    if(haloMesh && haloMesh.material && haloMesh.material.userData && haloMesh.material.userData.time)
    {
        zoomCompensation = rendering.camera.zoom.remap(.875, 1.75, 0.5, 1)
        haloMesh.material.color.lerp(currentColor, delta * 1.5);
        haloMesh.material.userData.time.value = clock.elapsedTime;
        haloMesh.material.userData.revealFactor.value = revealFactor * zoomCompensation;
    }

    if(splashMesh && splashMesh.material && splashMesh.material.userData && splashMesh.material.userData.time)
    {
        currentColor.multiplyScalar(0.2);
        origin.getWorldPosition(splashMesh.position);
        splashMesh.position.y = 0.1;
        splashMesh.material.color.lerp(currentColor, delta * 1.5);
        splashMesh.material.userData.time.value = clock.elapsedTime;
        splashMesh.material.userData.revealFactor.value = revealFactor * zoomCompensation;
    }
}

var orbEmissive = new THREE.Color(0x888888);

function getOrbMaterial()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
            fetch("static/prelude/prelude.orb.prefix.vertex").then(res => res.text()),
            fetch("static/prelude/prelude.orb.vertex").then(res => res.text()),
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];
            
            var material = new THREE.MeshLambertMaterial(
            { 
                color: 0xffffff,
                emissive: 0x888888,
            });

            material.userData.time = { value: 0 };
            material.userData.revealFactor = { value: 0 };

            material.customProgramCacheKey = () => "preludeOrb";
            material.onBeforeCompile = shader =>
            {
                shader.uniforms.time = material.userData.time;
                shader.uniforms.revealFactor = material.userData.revealFactor;

                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", body).replace("gl_PointSize = size", "gl_PointSize = pscale");
            };

            resolve({ main: material });
        });
    });
}

var dimColor = new THREE.Color(0x101010);
var brightColor = new THREE.Color(0x333333);
var currentColor = new THREE.Color(0x000000);

function getHaloMaterial()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
            fetch("static/prelude/prelude.halo.prefix.vertex").then(res => res.text()),
            fetch("static/prelude/prelude.halo.vertex").then(res => res.text()),
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];
            var fragPrefix = results[2];
            var fragBody = results[3];
            const textureLoader = new THREE.TextureLoader();
            sprite = textureLoader.load( './static/textures/halo.png', () =>
            {
                resolve({ main: material });
            });

            var material = new THREE.PointsMaterial(
            { 
                size: 1, 
                color: 0x333333,
                map: sprite, 
                blending: THREE.AdditiveBlending, 
                depthTest: true, 
                transparent: true,
                depthWrite: false 
            });

            material.userData.fillAmount = { value: 0.9 };
            material.userData.time = { value: 0 };
            material.userData.revealFactor = { value: 0 };

            material.customProgramCacheKey = () => "preludeHalo";

            material.onBeforeCompile = shader =>
            {
                shader.uniforms.fillAmount = material.userData.fillAmount;
                shader.uniforms.time = material.userData.time;
                shader.uniforms.revealFactor = material.userData.revealFactor;

                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", body).replace("gl_PointSize = size", "gl_PointSize = pscale");
            };

            //resolve({ main: material });
        });
    });
}

function getSplashMaterial()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
            fetch("static/prelude/prelude.splash.prefix.vertex").then(res => res.text()),
            fetch("static/prelude/prelude.splash.vertex").then(res => res.text()),
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];
            const textureLoader = new THREE.TextureLoader();
            sprite = textureLoader.load('./static/textures/halo.png', () => 
            {
                resolve({ main: material });
            });
            
            var material = new THREE.MeshBasicMaterial(
            { 
                color: 0x050505,
                map: sprite, 
                transparent: true, 
                depthTest:true, 
                depthWrite: false, 
                blending: THREE.AdditiveBlending,
            });

            material.userData.time = { value: 0 };
            material.userData.revealFactor = { value: 0 };

            material.customProgramCacheKey = () => "preludeSplash";
            material.onBeforeCompile = shader =>
            {
                shader.uniforms.time = material.userData.time;
                shader.uniforms.revealFactor = material.userData.revealFactor;
                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", body).replace("gl_PointSize = size", "gl_PointSize = pscale");
            };

            // resolve({ main: material });
        });
    });
}

function remap(value, in_min, in_max, out_min, out_max) 
{
    var v = (value - in_min) * (out_max - out_min) / (in_max - in_min) + out_min;
    var low = out_min;
    var high = out_max;
    if(out_max >= out_min)
    {
        low = out_min;
        high = out_max;
    }
    else
    {
        low = out_max;
        high = out_min;
    }

    return v.clamp(low, high);
}
