import * as THREE from './lib/three/build/three.module.js';
import * as rendering from "./renderer.js";
import * as environment from "./environment.js";
import * as prelude from './prelude.js';
import * as stage1 from './stage1.js';
import * as stage2 from './stage2.js';
import * as stage3 from './stage3.js';
import * as stage4 from './stage4.js';
import * as appData from "./appData.js";
import { AppViewModel } from "./appViewModel.js";
import { OrbitControls } from './lib/three/examples/jsm/controls/OrbitControls.js';
import { Animator } from "./revealAnimator.js";
import { HideAnimator } from "./hideAnimator.js";
import ko from 'knockout';
import audioManager from './AudioManager.ts';

//////////////////////////////////////////
var wrapper = document.getElementById("wrapper");
var container = document.getElementById("container");
var elWidth = 1920;
var elHeight = 1080;
var isPortraitMode = false;
var isPortraitModeRequired = false;

if (process.env.NODE_ENV !== 'production')
  console.log('Running in development mode')

var query = window.matchMedia("(orientation: portrait)");
onMediaQueryChange(query);
query.addListener(onMediaQueryChange);
window.addEventListener('resize', onWindowResize, false);

setRequiredOrientation();
doResize();
importProperStylesheet();

/////////////////////////////////////////////


var isInitialLoadComplete = false;
export var mainViewModel = new AppViewModel(appData.sectionData, isPortraitModeRequired);
var lookAtPoint = new THREE.Vector3(0, 0, 1);
var viewOffset = new THREE.Vector2();
var axis = new THREE.Vector3(0, 1, 0);
var animators = [
    new HideAnimator(0, 1),
    new Animator(new THREE.Vector3(0.8, 0.8, 0.8), new THREE.Euler(0.3, 1.7, 0.0, "XYZ")),
    new Animator(new THREE.Vector3(0.6, 0.6, 0.6), new THREE.Euler(-0.4, -0.13, 0.0, "XYZ")),
    new Animator(new THREE.Vector3(0.5, 0.5, 0.5), new THREE.Euler(0.0, 0.0, 0.0, "XYZ")),
    new Animator(new THREE.Vector3(1.0, 1.0, 1.0), new THREE.Euler(-0.4, 0.2, 0.0, "XYZ")),
]
axis.normalize();

const clock = new THREE.Clock();
const sceneContainer = document.getElementById("worldContainer");
let uiContainer, prevUiContainer;

init();
animate();

function init() {
    rendering.initialize(sceneContainer);
    ko.applyBindings(mainViewModel);
    lookAtPoint.copy(mainViewModel.lookAtPoint);
    viewOffset.copy(mainViewModel.targetViewOffset);

    audioManager.loadSounds();
    var loadTasks = [
        environment.initialize(rendering.scene),
        prelude.initialize(rendering.scene, mainViewModel, animators[0]),
        stage1.initialize(rendering.scene, mainViewModel, animators[1]),
        stage2.initialize(rendering.scene, mainViewModel, animators[2]),
        stage3.initialize(rendering.scene, mainViewModel, animators[3]),
        stage4.initialize(rendering.scene, mainViewModel, animators[4]),
    ];

    var tasksCompleted = 0;
    loadTasks.forEach((task, index) =>
    {       
        task.then(result => {
            var progress = ++tasksCompleted / loadTasks.length;
            mainViewModel.loadingProgress(Math.round(progress * 100) + "%");

          /*
            if(!isInitialLoadComplete && prelude.isLoaded && environment.isLoaded)
                onInitialLoadComplete();
                */
        })
    })

    Promise.all(loadTasks).then(() => {
      onInitialLoadComplete();
    })

    mainViewModel.currentSection.subscribe(onCurrentSectionChanged);
}

function onInitialLoadComplete()
{
    isInitialLoadComplete = true;

    uiContainer = document.getElementById('uiContainer');
    prevUiContainer = document.getElementById('prevUiContainer');
    document.getElementById("loadingContainer").style.display = "none";
    document.getElementById("wrapper").style.display = "";
    doResize();
    rendering.onWindowResize();
}

let hasHidden = false;
let oldSection = undefined;
const getInsightsStageNumberOfSection = (sectionKey) => {
  const regex = /insights(\d)/;
  const isInsights = regex.test(sectionKey);
  if (isInsights)
    return regex.exec(sectionKey)[1];
  return undefined;
}
let oldSectionKey;
function onCurrentSectionChanged(newSection) {
  audioManager.handleSectionChange(newSection, oldSection);
  // Give ui container class of current section so we can apply custom stylesj
  if (oldSection) {
    uiContainer.classList.remove(`section--${oldSection.key}`);
    prevUiContainer.classList.add(`section--${oldSection.key}`)
    if (oldSectionKey) prevUiContainer.classList.remove(`section--${oldSectionKey}`);
    oldSectionKey = oldSection.key;
  }
  uiContainer.classList.add(`section--${newSection.key}`);
  
    window.curSection = newSection;
    // Prelude visuals animate back in on restart
    if (oldSection && oldSection.key === 'results' && newSection.key === 'splash') {
      animators[0].startInverse(clock.elapsedTime, 1);
      if (revealInterval) window.clearInterval(revealInterval);
      animators[1].revealImmediate();
      animators[2].revealImmediate();
      animators[3].revealImmediate();
      animators[4].revealImmediate();
    }

    // Prepare scene for insights section
    if (newSection.key === 'calculating') {
      animators[0].startInverse(clock.elapsedTime, 1);
      animators[1].start(clock.elapsedTime, 1);
      animators[2].start(clock.elapsedTime, 1);
      animators[3].start(clock.elapsedTime, 1);
      animators[4].start(clock.elapsedTime, 1);
      hasHidden = false;
    } else if (newSection.key.includes('insights')) {
      if (!hasHidden) {
        // Animate out the prelude white balls once
        animators[0].start(clock.elapsedTime, 1);
        animators[1].hideImmediate();
        animators[2].hideImmediate();
        animators[3].hideImmediate();
        animators[4].hideImmediate();
        hasHidden = true;
      }
      const currentStage = getInsightsStageNumberOfSection(newSection.key);
      revealStage(currentStage, 1);
      const prevStage = getInsightsStageNumberOfSection(oldSection.key);
      if (prevStage) {
        hideStage(prevStage, 1);
      }
    } else if(newSection.key == "results") {
      if (oldSection.key === 'insights4') {
        hideStage(4, 2);
      } else {
        animators[4].hideImmediate();
        animators[0].start(clock.elapsedTime, 1);
      }

      animators[1].hideImmediate();
      animators[2].hideImmediate();
      animators[3].hideImmediate();

      revealAtFinal();
    } else
        reset();

    oldSection = newSection;
}

// Reveal and hide animations
function revealStage(stageNumber, duration) {
  animators[stageNumber].start(clock.elapsedTime, duration, 1);
}
function hideStage(stageNumber, duration) {
  animators[stageNumber].startInverse(clock.elapsedTime, duration);
}

let revealInterval = undefined;
const staggerDuration = 3;
function revealAtFinal()
{
    // Hide all stages

    environment.toggleRoomSpotlights(false);
    mainViewModel.isSaveAvailable(false);
    // Start the hide animator immediately

    // Reveal first stage after 0.5s
    setTimeout(() => {
      let currentStage = 1;
      revealStage(currentStage, staggerDuration);

      // Reveal subsequent stages after 3 seconds until finished
      revealInterval = setInterval(() => {
        currentStage+=1;
        if (currentStage < animators.length) revealStage(currentStage, 3);
        if (currentStage === animators.length -1) window.clearInterval(revealInterval);
      }, staggerDuration * 1000);
    }, 1 * 1000);

    setTimeout(() => {
        mainViewModel.isSaveAvailable(true);
    }, 12000);
}

function reset()
{
    environment.toggleRoomSpotlights(true);
}

function animate() 
{
    const delta = Math.min(clock.getDelta(), 1);

    prelude.update(clock, delta);
    stage1.update(clock, delta);
    stage2.update(clock, delta);
    stage3.update(clock, delta);
    stage4.update(clock, delta);

    if(mainViewModel.isFinalReveal) environment.finalRevealOrigin.rotateOnAxis(axis, delta * 0.1);

    
    pointCamera(delta);
    animateZoom(delta);
    animateViewOffset(delta);
    animateFillLight(delta);

    requestAnimationFrame(animate);
    render();
    //rendering.stats.update();
}

function animateZoom(delta)
{
    var targetZoomLevel = mainViewModel.currentSection().zoom;
    if(mainViewModel.isPortraitMode) targetZoomLevel *= 0.5;

    rendering.camera.zoom = THREE.MathUtils.lerp(rendering.camera.zoom, targetZoomLevel, 1.5 * delta);
    rendering.camera.updateProjectionMatrix();
}

function animateViewOffset(delta)
{
    viewOffset.lerp(mainViewModel.targetViewOffset, 1.5 * delta);
    rendering.updateViewOffset(viewOffset);
}

function pointCamera(delta)
{
    if(!mainViewModel.isPointCameraEnabled) return;
    lookAtPoint.lerp(mainViewModel.lookAtPoint, 1.5 * delta);
    const length = lookAtPoint.length();

    rendering.camera.lookAt(lookAtPoint);
}   

var targetColor = new THREE.Color();
function animateFillLight(delta)
{
    if(environment.stage == null) return;

    targetColor.copy(mainViewModel.currentSection().lightColor);

    if(mainViewModel.currentSectionIndex() <= 3)
    {
        var scalar = remap(mainViewModel.sections[3].currentQuestion().value(), 0, 1, -1, 1) * 0.08;
        targetColor.r += scalar;
        targetColor.g += scalar;
        targetColor.b += scalar;
    }
    environment.stage.material.color.lerp(targetColor, delta * 1.5);
}

function render() 
{
  rendering.render();
}

Number.prototype.remap = function (in_min, in_max, out_min, out_max) 
{
    var v = (this - in_min) * (out_max - out_min) / (in_max - in_min) + out_min;
    var low = out_min;
    var high = out_max;
    if(out_max >= out_min)
    {
        low = out_min;
        high = out_max;
    }
    else
    {
        low = out_max;
        high = out_min;
    }

    return v.clamp(low, high);
}

function remap(value, in_min, in_max, out_min, out_max) 
{
    var v = (value - in_min) * (out_max - out_min) / (in_max - in_min) + out_min;
    var low = out_min;
    var high = out_max;
    if(out_max >= out_min)
    {
        low = out_min;
        high = out_max;
    }
    else
    {
        low = out_max;
        high = out_min;
    }

    return v.clamp(low, high);
}

Number.prototype.clamp = function(min, max) {
  return Math.min(Math.max(this, min), max);
};



/////////////////////////////



function onWindowResize()
{
    doResize();
}

function onMediaQueryChange(e)
{
    isPortraitMode = e.matches
    doResize();
}

function checkOrientation()
{
    var wrongOrientation = document.getElementById("wrongOrientation");
    var uiContainer = document.getElementById("uiContainer");
    var orientLandscapeMessage = document.getElementById("orientLandscapeMessage");
    var orientPortraitMessage = document.getElementById("orientPortraitMessage");

    if(isPortraitMode && !isPortraitModeRequired && getDeviceType() != "desktop")
    {
        wrongOrientation.style.display = "";
        uiContainer.style.display = "none";
        orientLandscapeMessage.style.display = "";
        orientPortraitMessage.style.display = "none";
    }
    else if(!isPortraitMode && isPortraitModeRequired && getDeviceType() != "desktop")
    {
        wrongOrientation.style.display = "";
        uiContainer.style.display = "none";
        orientLandscapeMessage.style.display = "none";
        orientPortraitMessage.style.display = "";
    }
    else
    {
        wrongOrientation.style.display = "none";
        uiContainer.style.display = "";
    }
}

var MAX_RESIZE_ATTEMPTS = 5;
var STARTING_INTERVAL = 100;
var currentAttempt = 1;
var resizeTimeout;

function doResize() 
{
    const calculateResize = () => {
      checkOrientation();
      //if(!isPortraitMode)
      if(!isPortraitModeRequired)
      {
          var scale = Math.min(
              wrapper.clientWidth / elWidth,
              wrapper.clientHeight / elHeight
          );
          console.log(`Window resizing to ${scale}`);
          
          container.style.transform = "translate(-50%, -50%) scale(" + scale + ")";
      }
      else
      {
          container.style.transform = "translate(0%, 0%) scale(1)";
      }

      updateVH();
    }

    if (resizeTimeout) {
      window.clearTimeout(resizeTimeout);
      currentAttempt = 1;
    }

    const timeoutHandler = () => {
      calculateResize();
      currentAttempt ++;
      if (currentAttempt < MAX_RESIZE_ATTEMPTS) {
        resizeTimeout = setTimeout(timeoutHandler, Math.pow(2, currentAttempt) * STARTING_INTERVAL);
      }
    }

    timeoutHandler();
}

setInterval(updateVH, 500);
function updateVH()
{
    var zoom = window.screen.width / window.innerWidth;
    var vh = window.innerHeight * 0.01;
    if(getDeviceType() == "mobile") vh *= zoom;
    
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

window.doResize = doResize;

function setRequiredOrientation()
{
    switch (getDeviceType())
    {
        case "tablet":
            isPortraitModeRequired = false;
            break;
        case "mobile":
            isPortraitModeRequired = true;
            break;
        case "desktop":
            isPortraitModeRequired = false;
            break;
    }
    
    //isPortraitModeRequired = true;
}

function importProperStylesheet()
{
    if(isPortraitModeRequired)
        importStylesheet("static/mobile.css");
    else
        importStylesheet("static/landscape.css");             
}

function importStylesheet(href)
{
    var el = document.createElement("link");
    el.setAttribute("type", "text/css");
    el.setAttribute("rel", "stylesheet");
    el.setAttribute("href", href);
    document.getElementsByTagName("head")[0].appendChild(el);
}

function getDeviceType()
{
    const ua = navigator.userAgent;
    if (/(tablet|ipad|playbook|silk)|(android(?!.*mobi))/i.test(ua)) 
        return "tablet";

    if (/Mobile|iP(hone|od)|Android|BlackBerry|IEMobile|Kindle|Silk-Accelerated|(hpw|web)OS|Opera M(obi|ini)/.test(ua))
        return "mobile";
    
    //IPadOS workaround as it reports itself as a Intel based mac atleast for now. But no Macs exist with touch screen and won't for atleast a couple of years.
    if ((/\b(iPad)\b/.test(navigator.userAgent) && /WebKit/.test(navigator.userAgent) && !window.MSStream) || (navigator.platform === 'MacIntel' && navigator.maxTouchPoints && navigator.maxTouchPoints === 5)) 
        return "tablet";
    else
        return "desktop";
};
