import * as THREE from './lib/three/build/three.module.js';
import * as rendering from "./renderer.js";
import { GLTFLoader } from './lib/three/examples/jsm/loaders/GLTFLoader.js';

export var envMap;
export var finalRevealOrigin = new THREE.Object3D();
export var fillLight;
export var stage;
export var isLoaded = false;

const loader = new GLTFLoader();
var fullLighting;
var finalLighting;

export function	initialize(scene, gui)
{
    return new Promise((resolve, reject) =>
    {
        const textureLoader = new THREE.TextureLoader();
    	scene.background = new THREE.Color().setHSL(0.6, 0, 0);
        scene.fog = new THREE.Fog(scene.background, 1, 5000);

        //Reveal origin
        finalRevealOrigin.position.set(0, 2, 7);
        finalRevealOrigin.name = 'Final Reveal Origin';
        scene.add(finalRevealOrigin);

        // LIGHTS
        fillLight = new THREE.HemisphereLight(0x000000, 0x333333, 1);
        fillLight.groundColor.setHSL(0.095, 1, 0.75);
        fillLight.position.set(0, -50, 0);
        scene.add(fillLight);

        const dirLight = new THREE.DirectionalLight(0xffffff, 2);
        dirLight.color.setHSL(0.1, 1, 0.95);
        dirLight.position.set(0, 1.75, 0);
        dirLight.position.multiplyScalar(30);
        scene.add( dirLight );

        //Stage
        loadModel("static/models/stage.glb").then(loadResult =>
        {
            stage = loadResult.scene.children[0];
            stage.material = new THREE.MeshStandardMaterial({ color: 0x090909 });
            stage.position.set(0, 0, 0);
            stage.rotation.set(0, -3.14, 0);
            stage.scale.set(2.5, 2.5, 2.5);
            stage.castShadow = false;
            stage.receiveShadow = true;
            scene.add(stage);

            finalLighting = textureLoader.load('static/textures/StageColor.Up2.jpg');
            finalLighting.repeat = new THREE.Vector2(1, -1);
            finalLighting.wrapS = THREE.RepeatWrapping;
            finalLighting.wrapT = THREE.RepeatWrapping;


            fullLighting = textureLoader.load('static/textures/StageColor.Up.jpg', () => 
            {
                isLoaded = true;
                resolve()
            });
            fullLighting.repeat = new THREE.Vector2(1, -1);
            fullLighting.wrapS = THREE.RepeatWrapping;
            fullLighting.wrapT = THREE.RepeatWrapping;

            stage.material = new THREE.MeshBasicMaterial({ color: 0x444444 });
            stage.material.map = fullLighting;
            
            // isLoaded = true;
            // resolve();
        });

        //Env map
        var textureEquirec = textureLoader.load('static/textures/envMapB.jpg');
        textureEquirec.mapping = THREE.EquirectangularReflectionMapping;
        textureEquirec.encoding = THREE.sRGBEncoding;
        envMap = textureEquirec;
    });
}

export function toggleRoomSpotlights(roomLightsOn)
{
    stage.material.map = roomLightsOn ? fullLighting : finalLighting;
}

function loadModel(url)
{
    return new Promise((resolve, reject) => loader.load(url, (gltf) => resolve(gltf)));
}
