window.dataLayer = window.dataLayer || [];

type AnalyticsCategory = 'Navigation'|'Action'|'UserInput';

/*
 * Every time a user navigates to a new section, push a 'Navigation' GA event containing what that section is called.
 * If a clicks find out more/show my reflection, save image or start again push an 'Action' event.
 * Once a user completes one of the sections with an input field, push an event containing the user input data.
 */
const cleanupValue = (label: string, value: string) => {
  const lookup = {
    'Navigate to': {
      'intro': 'Onboarding 1',
      'onboarding': 'Onboarding 2',
      'prelude': 'Positivity',
      'section1': 'Connectivity',
      'section2': 'Productivity',
      'section3': 'Health and Wellbeing',
      'section4': 'Entertainment',
      'calculating': 'Before REFLCT',
      'insights1': 'Connectivity',
      'insights2': 'Productivity',
      'insights3': 'Health and Wellbeing',
      'insights4': 'Entertainment',
    },
    'Question result': {
      'prelude': 'Positivity',
      'section1': 'Connectivity',
      'section2': 'Productivity',
      'section3': 'Health and Wellbeing',
      'section4': 'Entertainment',
    }
  }
  const labelLookup = lookup[label];
  if (labelLookup) {
    const cleanedValue = labelLookup[value];
    if (cleanedValue) 
      return cleanedValue;
  }
  return value;
}

const notifyAnalytics = (type: AnalyticsCategory, label: string, value: string, extras: Record<string, unknown>) => {
  const data = {
    type,
    label,
    value: cleanupValue(label, value),
    ...extras,
  }
  console.table({...data})
  window.dataLayer.push({...data})
  /*
  if (gtag) {
    gtag('event', type, {
      label,
      value,
      ...extras,
    });
  } else {
  }
  */
}

export default notifyAnalytics;
