import Pizzicato from 'pizzicato';
import {tween} from 'shifty';


const AudioFxNames = ['next', 'prev', 'save', 'restart', 'findout'] as const;
type AudioFxNameTupple = typeof AudioFxNames;
type AudioFxName = AudioFxNameTupple[number];
type AudioFxOptions = {
  volume?: number,
}
const defaultOpts: AudioFxOptions = {
  volume: 1,
}
class AudioFx {
  sound?: Pizzicato.Sound;
  name: string;
  constructor(name: AudioFxName, opts?: AudioFxOptions) {
    this.name = name;
    this.opts = Object.assign({}, defaultOpts, opts);
  }
  load(): Promise<void> {
    return new Promise((res, rej) => {
      this.sound = new Pizzicato.Sound({
        source: 'file',
        options: { path: `static/audio/fx_${this.name}.mp3` },
      }, (error: Error|undefined) => {
        if (error) rej(error);
        else {
          if ( this.opts.volume !== undefined) {
            this.sound.volume = this.opts.volume;
          }
          res();
        }
      });
    });
  }
  play() {
    if( this.sound !== undefined ) this.sound.play();
  }
}

interface StageSound {
  sound: Pizzicato.Sound,
  effect: Pizzicato.Effects.HighPassFilter,
  volume: number,
  volumeMask: number,
  frequency: number,
  isPlaying: boolean,
};
interface loadSoundOptions {
  loop: boolean;
}
const buildStageSound = async(path: string, options: loadSoundOptions): Promise<StageSound> => {
  return new Promise((res, rej) => {
    const s = new Pizzicato.Sound({
      source: 'file',
      options: { path, loop: options.loop },
    }, (error: Error|undefined) => {
      if (error) {
        rej(error);
      } else {
        const effect = new Pizzicato.Effects.LowPassFilter({frequency: 400, peak: 10});
        s.addEffect(effect);
        res({
          sound: s,
          effect: effect,
          volume: 1,
          volumeMask: 0,
          frequency: 1,
          isPlaying: false,
        } as StageSound);
      }
    })
  });
}


const applyStageSoundParameters = (ss: StageSound) => {
    ss.sound.volume = ss.volume * ss.volumeMask
    ss.effect.frequency = ss.frequency;
    if (ss.sound.volume === 0 && ss.isPlaying) {
      ss.sound.stop();
      ss.isPlaying = false;
    }
    if (ss.sound.volume !== 0 && !ss.isPlaying) {
      ss.sound.stop();
      ss.sound.play();
      ss.isPlaying = true;
    }
}


class AudioManager {
  stageSounds: Record<number, StageSound> = {};
  fx: Record<AudioFxName, AudioFx> = {
    next: new AudioFx('next', { volume: 0.5 }),
    prev: new AudioFx('prev', { volume: 0.5 }),
    save: new AudioFx('save'),
    restart: new AudioFx('restart'),
    findout: new AudioFx('findout'),
  };
  constructor() {
  }

  loadSounds(): Promise<void> {
    return new Promise(async (res, rej) => {
      // Load audio fx
      Object.values(this.fx).forEach(fx => fx.load());

      const allSounds = await Promise.all([
        buildStageSound('static/audio/s0_landing.mp3', { loop: true }),
        buildStageSound('static/audio/s5_calculating.mp3', { loop: true }),
        buildStageSound('static/audio/s6_reveal.mp3', { loop: true }),
      ]);
      const [landing, calculating, reveal] = allSounds;
      this.stageSounds[0] = landing;
      this.stageSounds[5] = calculating;
      this.stageSounds[6] = reveal;
      res();
    });
  }

  async addStageSound(index: number, path: string) {
    const ss = await buildStageSound(path, { loop: true });
    this.stageSounds[index] = ss;
  }

  prepareStageSound(index: number) {
    if (this.stageSounds[index]) {
      const ss = this.stageSounds[index];
      ss.sound.volume = 0;
      ss.sound.play();
    }
  }

  handleSectionChange(section: unknown, lastSection: unknown) {
    const {key} = section;
    const currentStageLookup = {
      'splash': 7,
      'intro': 0,
      'onboarding': 0,
      'prelude': 7,
      'section1': 1,
      'section2': 2,
      'section3': 3,
      'section4': 4,
      'calculating': 5,
      'insights1': 7,
      'insights2': 7,
      'insights3': 7,
      'insights4': 7,
      'results': 6,
    }
    const activeStageIndex = currentStageLookup[key];
    const prevStageIndex = lastSection ? currentStageLookup[lastSection.key] : undefined;
    if (activeStageIndex !== undefined && activeStageIndex !== prevStageIndex) this.fadeInStageSound(activeStageIndex);
    if (prevStageIndex !== undefined && prevStageIndex !== activeStageIndex) this.fadeOutStageSound(prevStageIndex);
  }

  updateStageSound(stageNumber: number, options: { volumeMask?: number, volume?: number, frequency?: number }) {
    const { volume, volumeMask, frequency } = options;
    const s = this.stageSounds[stageNumber];
    if (!s) {
      return;
    }
    if (volume) s.volume = volume;
    if (volumeMask) s.volumeMask = volumeMask;
    if (frequency) s.frequency = frequency;

    applyStageSoundParameters(s);
  }

  animateStageSoundMask(stageNumber: number, targetVolume: number, duration = 3) {
    const ss = this.stageSounds[stageNumber];
    if (!ss) {
      return;
    }
    const currentMask = ss.volumeMask
    tween({
      render: ({volumeMask}) => {
        this.updateStageSound(stageNumber, { volumeMask });
      },
      easing: 'easeInOutQuad',
      duration: duration * 1000,
      from: {volumeMask: currentMask},
      to: { volumeMask: targetVolume},
    })
  }

  fadeInStageSound(stageNumber: number) {
    this.animateStageSoundMask(stageNumber, 1, 3);
  }
  fadeOutStageSound(stageNumber: number) {
    this.animateStageSoundMask(stageNumber, 0, 3);
  }

  playFx(soundFx: AudioFxName) {
    this.fx[soundFx].play();
  }
}

const audioManager = new AudioManager();
window.aa = audioManager;
export default audioManager;
