import * as THREE from './lib/three/build/three.module.js';

export function HideAnimator()
{
	var self = this;
	var startTime;
	var elapsed = 0;
	var t = 0;
	var callback;
  let duration = 0;

	self.isActive = false;

	self.setCallback = function(inCallback)
	{
		callback = inCallback;
	}
	
	self.start = function(inCurrentTime, animationDuration)
	{
		callback(0);
		startTime = inCurrentTime;
		self.isActive = true;
    self.isInverse = false;
    duration = animationDuration;
	}

	self.startInverse = function(inCurrentTime, animationDuration)
	{
		callback(1);
		startTime = inCurrentTime;
		self.isActive = true;
    self.isInverse = true;
    duration = animationDuration;
	}

  self.hideImmediate = function() {
    callback(0, false);
  }

	self.update = function(clock)
	{
		if(!self.isActive) return;

		if(clock.elapsedTime >= startTime)
		{
			elapsed = clock.elapsedTime - startTime;
	        t = elapsed / duration;
	        t = t * t; //Easy in
	        //t = 1 - (1 - t) * (1 - t); //Ease out
	        
	        if(clock.elapsedTime >= startTime + duration)
	        {
	            self.isActive = false;
	            t = 1;
	        }
          if (!self.isInverse) {
            callback(1 - t);
          } else {
            callback(t);
          }
	    }
	}
}
