import * as THREE from './lib/three/build/three.module.js';
import { GLTFLoader } from './lib/three/examples/jsm/loaders/GLTFLoader.js';
import * as environment from "./environment.js";
import { Animator } from "./revealAnimator.js";
import * as rendering from "./renderer.js";
import audioManager from './AudioManager.ts';

const loader = new GLTFLoader();
var mainMesh;
var secondaryMesh;
var splashMesh;
var scene;
var viewModel;
var fillWidth = 0.1;
var currentFillAmount1 = 0;
var currentFillWidth = fillWidth;
var currentFillAmount2 = 0;
var speed = 0.01;
var center;
var sectionPlacement;
var origin;
var revealAnimator;
var isRevealed;
var worldForward = new THREE.Vector3(0, 0, 1);
var originForward = new THREE.Vector3();

export function	initialize(providedScene, mainViewModel, inRevealAnimator)
{
    return new Promise((resolve, reject) => 
    {
        viewModel = mainViewModel.getSectionByKey("section3").section;
        currentFillAmount1 = viewModel.questions[0].value();
        currentFillAmount2 = viewModel.questions[1].value();
        mainViewModel.currentSection.subscribe(onCurrentSectionChanged);
        viewModel.isLoading(true);

        scene = providedScene;
        center = new THREE.Object3D();
        origin = new THREE.Object3D();
        origin.name = 'Stage3 Origin';
        sectionPlacement = new THREE.Object3D();
        sectionPlacement.position.set(0, 2, -10);
        center.rotation.set(0, 0.628 * -3, 0);
        sectionPlacement.add(origin);
        center.add(sectionPlacement);
        scene.add(center);
        center.name = 'Stage3 Center';
        viewModel.modelOrigin = origin;
        revealAnimator = inRevealAnimator;
        revealAnimator.setSourceSection(viewModel);

        Promise.all([
            loadQ5(),
            loadQ6(),
            audioManager.addStageSound(3, 'static/audio/s3_health.mp3'),
        ]).then(results => 
        {
            viewModel.isLoading(false);
            if(mainViewModel.currentSection() == mainViewModel) show();
            resolve();
        });
    });
}

let hasMovedToOrigin = false;
function onCurrentSectionChanged(newSection)
{
    if (newSection.key.includes('insights')) {
      moveToFinal();
    } else if(newSection.key == "results") {
        moveToFinal();
        revealAtFinal();
    } else {
      reset();
    }
}

export function reset()
{
    hasMovedToOrigin = false;
    environment.finalRevealOrigin.remove(origin);
    sectionPlacement.add(origin);
    origin.scale.set(1, 1, 1);
    origin.rotation.set(0, 0, 0);
    isRevealed = false;
}

export function moveToFinal() {
  if (!hasMovedToOrigin) {
    hasMovedToOrigin = true;
    environment.finalRevealOrigin.add(origin);
  }
}

export function revealAtFinal()
{
    isRevealed = true;
}

function loadQ5()
{
    return new Promise((resolve, reject) => 
    {
        Promise.all([
            loadModel("static/models/q5.glb"),
            getQ5Material()
        ]).then(results =>
        {
            var model = results[0];
            var materials = results[1];
        
            var instanceCount = 50;
            var geo = model.scene.children[0].geometry;

            mainMesh = new THREE.InstancedMesh(geo, materials.main, instanceCount);
            mainMesh.customDepthMaterial = materials.depth;

            const position = new THREE.Vector3();
            const rotation = new THREE.Euler();
            const quaternion = new THREE.Quaternion();
            const scale = new THREE.Vector3(1, 1, 1);
            const matrix = new THREE.Matrix4();

            for(var i=0;i<instanceCount;i++)
            {
                matrix.compose(position, quaternion, scale);
                mainMesh.setMatrixAt(i, matrix);
                mainMesh.setColorAt(i, new THREE.Color(i / (instanceCount - 1), 0, 0));
            }
            mainMesh.needsUpdate = true;
            origin.add(mainMesh);
            resolve();
        });
    });
}

function loadModel(url)
{
    return new Promise((resolve, reject) => loader.load(url, (gltf) => resolve(gltf)));
}

function loadQ6()
{
    return new Promise((resolve, reject) => 
    {
        Promise.all([
            loadModel("static/models/q6.points.glb"),
            loadModel("static/models/q6.instance.glb"),
            getQ6Material(),
            getSplashMaterial()
        ]).then(results => {
            var pointsModel = results[0];
            var instanceModel = results[1];
            var materials = results[2];   
            var splashMaterial = results[3].main;
            var positionData = pointsModel.scene.children[0].geometry.attributes.position;
            var instanceCount = positionData.count; 
            const position = new THREE.Vector3();
            const rotation = new THREE.Euler();
            const quaternion = new THREE.Quaternion();
            const scale = new THREE.Vector3(1, 1, 1);
            const matrix = new THREE.Matrix4();
            const scalar = 0.1;

            secondaryMesh = new THREE.Points(pointsModel.scene.children[0].geometry, materials.main);
            secondaryMesh.scale.set(.15, .15, .15);

            //Ground Light Splash
            var planeGeo = new THREE.PlaneBufferGeometry(1, 1);
            splashMesh = new THREE.InstancedMesh(planeGeo, splashMaterial, instanceCount);
            splashMesh.scale.copy(secondaryMesh.scale);
            splashMesh.position.set(0, -sectionPlacement.position.y + 0.1, 0);
            quaternion.setFromEuler(new THREE.Euler(-1.57, 0, 0));
            scale.set(1, 1, 1);
            var instanceColor = new THREE.Color(0xffffff);
            for(var i=0;i<instanceCount;i++)
            {
                position.set(0, 0, 0);
                matrix.compose(position, quaternion, scale);
                splashMesh.setMatrixAt(i, matrix);
                splashMesh.setColorAt(i, instanceColor);
            }
            splashMesh.instanceColor.needsUpdate = true;

            planeGeo.setAttribute("instancePosition", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes.position.array, 3));
            planeGeo.setAttribute("instanceAxis", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._axis.array, 3));
            planeGeo.setAttribute("instancePScale", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._pscale.array, 1));
            planeGeo.setAttribute("instanceSpeed", new THREE.InstancedBufferAttribute(pointsModel.scene.children[0].geometry.attributes._speed.array, 1));
            splashMesh.needsUpdate = true;
            scene.add(splashMesh);
            origin.add(secondaryMesh);

            resolve();
        });
    });
}

export function update(clock, delta)
{
    var zoomCompensation;
    revealAnimator.update(clock);

    currentFillAmount1 = THREE.Math.lerp(currentFillAmount1, viewModel.questions[0].value(), 2 * delta);
    currentFillAmount2 = THREE.Math.lerp(currentFillAmount2, viewModel.questions[1].value(), 1.5 * delta);
    currentFillWidth = THREE.Math.lerp(currentFillWidth, viewModel.questions[1].value(), 2 * delta);
    audioManager.updateStageSound(3, { 
      volume: viewModel.questions[0].value() * 0.5 + 0.5,
      frequency: viewModel.questions[1].value() * 2200 + 800,
    });

    if(!isRevealed) origin.rotation.set(0, currentFillAmount1 - currentFillAmount2, 0);

    if(mainMesh && mainMesh.material && mainMesh.material.userData && mainMesh.material.userData.fillAmount)
    {
        if(mainMesh.material.envMap != environment.envMap)
        {
            mainMesh.material.envMap = environment.envMap;
            mainMesh.material.needsUpdate = true;
        }

        mainMesh.material.userData.time.value = clock.elapsedTime;
        mainMesh.material.userData.fillAmount.value = currentFillAmount1;
        mainMesh.material.userData.fillWidth.value = currentFillWidth;
        mainMesh.material.userData.speed.value = speed;
    }

    if (hasMovedToOrigin && !isRevealed) {
      const slideValue = (1 - revealAnimator.mix) * 0.2;
      if (rendering.isPortraitMode) {
        origin.position.set(0, slideValue, 0);
      } else {
        origin.position.set(slideValue, 0, 0);
      }
    }

    if(secondaryMesh && secondaryMesh.material && secondaryMesh.userData && secondaryMesh.material.userData.time)
    {
        secondaryMesh.material.userData.time.value = clock.elapsedTime;
        secondaryMesh.material.userData.fillAmount.value = currentFillAmount2;

        zoomCompensation = rendering.camera.zoom.remap(.7, 1.75, 0.3, 0.8);
        secondaryMesh.material.size = zoomCompensation;
    }

    if(splashMesh && splashMesh.material && splashMesh.userData && splashMesh.material.userData.time)
    {
        splashMesh.material.userData.time.value = clock.elapsedTime;
        splashMesh.material.userData.fillAmount.value = currentFillAmount2;

        origin.getWorldPosition(splashMesh.position);
        splashMesh.position.y = 0.1;
        splashMesh.scale.copy(origin.scale);
        splashMesh.scale.multiplyScalar(0.15);
        splashMesh.material.size = zoomCompensation;
        origin.getWorldDirection(originForward);
        originForward.y = 0;
        originForward.normalize();
        splashMesh.quaternion.setFromUnitVectors(worldForward, originForward);
    }

}

function getQ5Material()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
        	fetch("static/stage3/q5.prefix.vertex").then(res => res.text()),
            fetch("static/stage3/q5.vertex").then(res => res.text())
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];

            var material = new THREE.MeshPhysicalMaterial(
            {
                color: 0xffffff,
                metalness: 0.0,
                roughness: 0.15,
                morphTargets: false,
                vertexColors: false
            });

            material.userData.fillAmount = { value: 0.9 };
            material.userData.fillWidth = { value: 0.0 };
            material.userData.speed = { value: 0.0 };
            material.userData.time = { value: 0 };

            material.customProgramCacheKey = () => "q5";

            material.onBeforeCompile = shader =>
            {
                shader.defines.FULL = "";
                shader.uniforms.fillAmount = material.userData.fillAmount;
                shader.uniforms.fillWidth = material.userData.fillWidth;
                shader.uniforms.speed = material.userData.speed;
                shader.uniforms.time = material.userData.time;
                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", body);
            };

            resolve({ main: material });
        });
    });
}

function getQ6Material()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
            fetch("static/stage3/q6.prefix.vertex").then(res => res.text()),
            fetch("static/stage3/q6.vertex").then(res => res.text()),
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];

            const textureLoader = new THREE.TextureLoader();
            const sprite = textureLoader.load( './static/textures/orb.png' );
            var material = new THREE.PointsMaterial(
            { 
                size: 1, 
                color: 0xffffaa,
                map: sprite, 
                blending: THREE.AdditiveBlending, 
                depthTest: true, 
                transparent: true,
                depthWrite: false 
            });

            material.userData.fillAmount = { value: 0.9 };
            material.userData.time = { value: 0 };

            material.customProgramCacheKey = () => "q6";
            material.onBeforeCompile = shader =>
            {
                shader.uniforms.fillAmount = material.userData.fillAmount;
                shader.uniforms.time = material.userData.time;
                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", body).replace("gl_PointSize = size", "gl_PointSize = pscale");
            };

            resolve({ main: material });
        });
    });
}

function getSplashMaterial()
{
    return new Promise((resolve, reject) =>
    {
        Promise.all([
            fetch("static/stage3/q6.splash.prefix.vertex").then(res => res.text()),
            fetch("static/stage3/q6.splash.vertex").then(res => res.text()),
        ])
        .then(results =>
        {
            var prefix = results[0];
            var body = results[1];
            const textureLoader = new THREE.TextureLoader();
            var sprite = textureLoader.load( './static/textures/halo.png' );
            
            var material = new THREE.MeshBasicMaterial(
            { 
                color: 0x060605,
                map: sprite, 
                transparent: true, 
                depthTest:true, 
                depthWrite: false, 
                blending: THREE.AdditiveBlending,
            });

            material.userData.time = { value: 0 };
            material.userData.fillAmount = { value: 0.9 };

            material.customProgramCacheKey = () => "q6Splash";
            material.onBeforeCompile = shader =>
            {
                shader.uniforms.time = material.userData.time;
                shader.uniforms.fillAmount = material.userData.fillAmount;
                shader.vertexShader = prefix + "\n" + shader.vertexShader.replace("#include <project_vertex>", body).replace("gl_PointSize = size", "gl_PointSize = pscale");
            };

            resolve({ main: material });
        });
    });
}

function remap(value, in_min, in_max, out_min, out_max) 
{
    var v = (value - in_min) * (out_max - out_min) / (in_max - in_min) + out_min;
    var low = out_min;
    var high = out_max;
    if(out_max >= out_min)
    {
        low = out_min;
        high = out_max;
    }
    else
    {
        low = out_max;
        high = out_min;
    }

    return v.clamp(low, high);
}
